@import './variables';
@import './mixins';

* {
    margin: 0;
    padding: 0;
}

body {
    background: #f8f8f8;
    font-family: 'dejavu sans', sans-serif;
    overflow-x: hidden;
}

nav {
    background: linear-gradient(to right, #123f88, #152b54);
    position: fixed;
    z-index: 999;
}

h1, h2, h3, p {
    color: #152b54;
}

h1, h2 {
    text-transform: uppercase;
}

.demo {
    color: #f8f8f8;
    text-decoration: none;
}

.footer {
    background: linear-gradient(to right, #123f88, #152b54);
    
    h2 {
        color: #fff;
    }

    ul {
        li {
            color: #fff;
            list-style: none;
        } 
    }
}

.preload{
    background-color:#fff;
    background-image: url('./../img/loader.svg');
    background-position:50% 50%;
    background-repeat:no-repeat;    
    position: fixed;
    top:0; left:0;
    width:100%; height:100%;
    z-index:99999;
}


@media only screen and (min-width: 320px) {
    h1 {
        font-size: 7vw;
        font-weight: 400;
        text-align: center;
    }
    h2 {
        font-size: 6vw;
        font-weight: 400;
    }

    .card {
        border: #002E5C .2vw solid;
        border-radius: 2vw;
        display: grid;
        grid-template: 
        "icon title"
        "text text"; 
        grid-template-columns: 1fr 5fr;
        width: 80vw;        
        h3 {
            grid-area: title;
            font-size: 5.5vw;
            font-weight: 400;
            padding: 4vw 0;
        }
        p {
            grid-area: text;
            font-size: 4.5vw;
            font-weight: 200;
            padding: 0 4vw 4vw 4vw;
        }

        img{
            grid-area: icon;
            padding: 4vw 0 4vw 4vw;
            width: 8vw;
        }
    }

    .chart {
    }

    .container {
        display: grid;
        grid-template-columns: 100vw;
        grid-template-rows: 85vh;
        padding-top: 20vw;
    }

    .demonstration {
        display: grid;
        grid-template-rows: 10vh 90vh;
        grid-template-columns: 100vw;
        justify-content: center;
        justify-items: center;
        align-items: center;        
        background-color: #eee;
    }

    .description {
        font-size: 5vw;
        font-weight: 200;
        max-width: 80vw;
    }

    .footer {
        display: grid;
        grid-template-columns: 50vw 50vw;
        height: 20vh;
        margin-top: 2vw;
        h2 {
            
        }

        ul {
            li {
            } 
        }
    }

    .growing{
        width: 80vw;
    }
    
    .header {
        background: url('./../img/bg-08.svg'), url('./../img/bg-09.svg');
        background-repeat: no-repeat;
        background-position: 10vw 0vw, 0vw 30vw;
    }

    .homem {
    }

        

    .menu {
        display: grid;
        grid-template-columns: 50vw 50vw;
        width: 100vw;
        .icon {
            padding: 5vw;
            width: 10vw;
        }
        svg{
            @include box(10vw, 10vw);
            display: block;
        }
        
        .demo {
            align-self: center;        
            background: linear-gradient(to right, #002E5C, #004488);
            border-radius: 20/3.2+vw;
            box-shadow: #123223 -2/3.2+vw 2/3.2+vw 2/3.2+vw;
            justify-self: end;
            margin-right: 5vw;
            padding: 2vw 4vw;
            cursor: pointer;
    
            &:hover {
                background: linear-gradient(to left, #002E5C, #004488);
            }
            
            &:focus {
                background: linear-gradient(to top, #002E5C, #004488);
            }
        }
    }

    .ex {
        #path2752 {
            animation: 300ms oneRotare forwards;
            animation-delay: 300ms;
            transform-origin: 52.8/3.2+vw 75.54/3.2+vw;
        }
        #path2762 {
            animation: 300ms desapear linear forwards;
            transform-origin: 50.25/3.2+vw 77.15/3.2+vw;
        }
        #path2766 {
            animation: 300ms thirdRotare forwards;
            animation-delay: 600ms;
            transform-origin: 54.4/3.2+vw 78.1/3.2+vw;
        }
    }

    @keyframes oneRotare {
        0% { 
            transform: rotate(0);
        }
        100% { 
            stroke: #1e2f5e;
            transform: rotate(-45deg);
        }
    }

    @keyframes desapear {
        0% { 
            opacity: 1;
            transform: rotate(0deg)
        }
        100% { 
            opacity: 0;
            transform: rotate(360deg) 
        }
    }

    @keyframes thirdRotare {
        0% { 
            transform: rotate(0);
        }
        100% { 
            stroke: #1e2f5e;
            transform: rotate(45deg);
        }
    }
    
    .marketing {
        width: 78vw;
    }

    .mulher {
        width: 80vw;
    }

    .miner {
        background: url('./../img/bg-10.svg'), url('./../img/bg-11.svg');
        background-repeat: no-repeat;
        background-position: 20vw 20vw, -20vw 55vw;
    }

    .second {
        background: url('./../img/bg-03.svg'), url('./../img/bg-04.svg');
        background-repeat: no-repeat;
        background-position: 20vw 20vw, -20vw 55vw;
    }

    .section {
        align-content: space-around;
        display: grid;
        justify-content: center;
        justify-items: center;
    }

    .services {
        background: url('./../img/bg-05.svg'), url('./../img/bg-06.svg'), url('./../img/bg-07.svg');
        background-repeat: no-repeat;
        background-size: 50vw;
        background-position: 40vw 25vw, 10vw 75vw, 40vw 120vw;

        //APAGAR
        margin-bottom: 10vw;
    }

    .third {
        background: url('./../img/bg-01.svg'), url('./../img/bg-02.svg');
        background-repeat: no-repeat;
        background-position: 20vw 15vw, -10vw 60vw;
    }

    .triangle {
        
    }

    [data-animate] {
        opacity: 0;
        transition: .5s;
    }

    [data-animate="left"] {
        transform: translate3D(-5vw, 0, 0);
    }

    [data-animate="right"] {
        transform: translate3D(0, 5vw, 0);
    }

    [data-animate="bottom"] {
        transform: translate3D(0, -5vw, 0);
    }

    [data-animate].animate {
        opacity: 1;
        transform: translate3D(0, 0, 0);
    }
}

